<template>
  <div id="work-shift-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="workShiftFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w w-full required">
          <label class="vs-input--label">{{ $t('DAY') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="weekDayEnumId">
            <vs-select-item v-for="(enumeration, index) in weekDays" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row mb-4 timepicker-wrap">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('START_TIME') }}</label>
          <div class="vs-inputx vs-input--input bg-white w-full">
            <vue-timepicker v-model="startTime" format="HH:mm:ss"></vue-timepicker>
          </div>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('END_TIME') }}</label>
          <div class="vs-inputx vs-input--input bg-white w-full">
            <vue-timepicker v-model="endTime" format="HH:mm:ss"></vue-timepicker>
          </div>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" type="filled" @click="createBranchWorkShift">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" type="border" @click="hideWorkShiftForm">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="workShiftTableData" :noDataText="workShiftTableNoDataText" :sst="true" search @search="filterWorkShiftTable" @sort="orderWorkShiftTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showWorkShiftForm" :disabled="workShiftFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshWorkShiftTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="workShiftTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ workShiftTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="WorkShiftTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="weekDayDescription">
          {{ this.$t('DAY') }}
        </vs-th>
        <vs-th sort-key="startTime">
          {{ this.$t('START_TIME') }}
        </vs-th>
        <vs-th sort-key="endTime">
          {{ this.$t('END_TIME') }}
        </vs-th>
        <vs-th>
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].weekDayDescription">
            {{ data[index].weekDayDescription }}
          </vs-td>
          <vs-td :data="data[index].startTime">
            {{ data[index].startTime | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].endTime">
            {{ data[index].endTime }}
          </vs-td>
          <vs-td :data="data[index].options">
            <vs-button class="p-3 mr-4" color="danger" @click="deleteBranchWorkShift(data[index].shiftSeqId)" icon-pack="far" icon="fa-trash-alt">{{ $t('DELETE') }}</vs-button>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="workShiftTableTotalPages" v-model="workShiftTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import TimePickerMixin from '@/mixins/commons/TimePickerMixin'
import BranchService from '@/services/bank/BranchService'
import EnumerationService from '@/services/commons/EnumerationService'
import VueTimepicker from 'vue2-timepicker'

export default {
  name: 'branch-work-shift',
  mixins: [LoadingMixin, ListLayoutMixin, TimePickerMixin],
  components: {
    VueTimepicker
  },
  props: {
    branchId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      workShiftTableData: [],
      workShiftTableFilter: null,
      workShiftTableFilterTimer: null,
      workShiftTableOrderBy: null,
      workShiftTableContainer: 'work-shift-loading-container',
      workShiftTableBusy: false,
      workShiftTableTotalRows: 0,
      workShiftTableCurrentPage: 1,
      workShiftTablePerPage: 10,
      workShiftFormVisible: false,
      weekDayEnumId: 'DowMonday',
      weekDays: [],
      startTime: {
        HH: '00',
        mm: '00',
        ss: '00'
      },
      endTime: {
        HH: '00',
        mm: '00',
        ss: '00'
      }
    }
  },
  computed: {
    workShiftTableNoDataText: function () {
      return this.workShiftTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    workShiftTableTotalPages: function () {
      return Math.ceil(this.workShiftTableTotalRows / this.workShiftTablePerPage)
    },
    workShiftTablePagingInfo: function () {
      return this.pagingInfo(this.workShiftTableCurrentPage, this.workShiftTablePerPage, this.workShiftTableTotalRows)
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'DayOfWeek',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.weekDays = response.data.enumerationList || []
      })
    },
    loadWorkShiftTableData (loadingContainerId) {
      let self = this
      this.workShiftTableBusy = true
      this.workShiftTableContainer = loadingContainerId || this.workShiftTableContainer
      this.startLoading(this.workShiftTableContainer)
      BranchService.workShifts({
        pageIndex: this.workShiftTableCurrentPage - 1,
        pageSize: this.workShiftTablePerPage,
        filter: this.workShiftTableFilter,
        orderByField: this.workShiftTableOrderBy,
        branchId: this.branchId
      }).then(function (response) {
        self.stopLoading(self.workShiftTableContainer)
        self.workShiftTableBusy = false
        self.workShiftTableTotalRows = response.data.totalRows
        self.workShiftTableData = response.data.workShiftList || []
      })
    },
    refreshWorkShiftTable () {
      this.loadWorkShiftTableData()
    },
    filterWorkShiftTable (filter) {
      let self = this
      if (this.workShiftTableFilter !== filter) {
        clearTimeout(this.workShiftTableFilterTimer)
        this.workShiftTableFilterTimer = setTimeout(() => {
          self.workShiftTableFilter = filter
          self.loadWorkShiftTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.workShiftTableFilterTimer)
      }
    },
    orderWorkShiftTable (orderBy, active) {
      this.workShiftTableOrderBy = (active ? '' : '-') + orderBy
      this.loadWorkShiftTableData()
    },
    showWorkShiftForm () {
      this.workShiftFormVisible = true
    },
    hideWorkShiftForm () {
      this.workShiftFormVisible = false
    },
    workShiftDetails (item) {
      if (item.workShiftTypeEnumId === 'CmtTelecomNumber') {
        let phoneNumber = '+'
        phoneNumber += item.countryCode ? item.countryCode : ''
        phoneNumber += item.areaCode ? '-' + item.areaCode : ''
        phoneNumber += item.contactNumber ? '-' + item.contactNumber : ''
        phoneNumber += item.extension ? 'x' + item.extension : ''
        return phoneNumber
      } else {
        return item.infoString
      }
    },
    createBranchWorkShift () {
      let self = this
      BranchService.createWorkShift({
        branchId: this.branchId,
        startTime: this.timeToString(this.startTime),
        endTime: this.timeToString(this.endTime),
        weekDayEnumId: this.weekDayEnumId
      }).then(function (response) {
        self.refreshWorkShiftTable()
        self.hideWorkShiftForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    deleteBranchWorkShift (shiftSeqId) {
      let self = this
      BranchService.deleteWorkShift({
        branchId: this.branchId,
        shiftSeqId: shiftSeqId
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshWorkShiftTable()
        }
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  mounted () {
    this.load()
    this.loadWorkShiftTableData()
  },
  watch: {
    workShiftTableCurrentPage: function () {
      this.refreshWorkShiftTable()
    },
    workShiftTablePerPage: function () {
      this.refreshWorkShiftTable()
    }
  }
}
</script>
