<template>
  <div id="contact-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="contactMechFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w w-full">
          <div class="flex justify-end">
            <span><i class="far fa-phone"></i></span>
            <vs-switch class="mx-2" v-model="contactMechEmail"/>
            <span><i class="far fa-at"></i></span>
          </div>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required" v-if="!contactMechEmail">
          <label>{{ $t('PHONE_NUMBER') }}</label>
          <div>
            <div class="float-left mx-2 pt-2">+</div>
            <vs-input class="float-left w-16" v-model="countryCode" :title="$t('COUNTRY_CODE')"/>
            <span class="float-left mx-2 pt-2">-</span>
            <vs-input class="float-left w-16" v-model="areaCode" :title="$t('AREA_CODE')"/>
            <span class="float-left mx-2 pt-2">-</span>
            <vs-input class="float-left w-100" v-model="contactNumber" :title="$t('PHONE_NUMBER')"/>
            <span class="float-left mx-2 pt-2">x</span>
            <vs-input class="float-left w-16" v-model="extension" :title="$t('EXTENSION')"/>
            <div class="clearfix"></div>
          </div>
        </div>
        <div class="nv-col sm:w-1/2 w-full required" v-if="contactMechEmail">
          <label>{{ $t('EMAIL_ADDRESS') }}</label>
          <vs-input class="w-full" type="email" v-model="infoString" :title="$t('EMAIL_ADDRESS')"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label>{{ $t('PURPOSE') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="contactMechPurposeId">
            <vs-select-item v-for="(contactMechPurpose, index) in contactMechPurposes" :key="index" :value="contactMechPurpose.contactMechPurposeId" :text="contactMechPurpose.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createBranchContactMech" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideContactMechForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <div v-show="!contactMechCardLayout">
      <vs-table class="mb-base" :data="contactMechTableData" :noDataText="contactMechTableNoDataText" :sst="true" search @search="filterContactMechTable" @sort="orderContactMechTable">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="showContactMechForm" :disabled="contactMechFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far">{{ $t('ADD') }}</vs-button>
            <vs-button class="p-3 mr-4" @click="refreshContactMechTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="contactMechTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="flex items-center justify-between mr-4">
              <span><i class="far fa-table"></i></span>
              <vs-switch class="mx-2" v-model="contactMechCardLayout"/>
              <span><i class="far fa-address-card"></i></span>
            </div>

            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ contactMechTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="contactMechTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="contactMechTypeDescription">
            {{ this.$t('TYPE') }}
          </vs-th>
          <vs-th sort-key="details">
            {{ this.$t('DETAILS') }}
          </vs-th>
          <vs-th sort-key="contactMechPurposeDescription">
            {{ this.$t('PURPOSE') }}
          </vs-th>
          <vs-th>
            {{ this.$t('OPTIONS') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].contactMechTypeDescription">
              {{ data[index].contactMechTypeDescription }}
            </vs-td>
            <vs-td :data="data[index].details">
              {{ contactMechDetails(data[index]) | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].contactMechPurposeDescription">
              {{ data[index].contactMechPurposeDescription }}
            </vs-td>
            <vs-td :data="data[index].options">
              <vs-button class="p-3 mr-4" color="danger" @click="deleteBranchContactMech(data[index].contactMechId)" icon-pack="far" icon="fa-trash-alt">{{ $t('DELETE') }}</vs-button>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="contactMechTableTotalPages" v-model="contactMechTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
    <div v-show="contactMechCardLayout">
      <div class="w-full mb-base">
        <div class="flex justify-between">
          <div class="flex">
            <vs-button class="p-3 mr-4" @click="refreshContactMechTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="contactMechTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>
          <div class="flex justify-end">
            <div class="flex items-center justify-between">
              <span><i class="far fa-table"></i></span>
              <vs-switch class="mx-2" v-model="contactMechCardLayout"/>
              <span><i class="far fa-address-card"></i></span>
            </div>
          </div>
        </div>
      </div>

      <div class="alert-container" v-if="contactMechTableData.length === 0">
        <vs-alert :active.sync="contactMechTableData.length === 0" color="warning">
          <i class="far fa-exclamation-circle pr-1"></i> {{ $t('NO_DATA_FOUND') }}
        </vs-alert>
      </div>
      <div class="vs-row flex" v-if="contactMechTableData.length > 0">
        <div class="nv-col sm:w-1/3 w-full p-2" v-for="(contactMechCard, index) in contactMechTableData" :key="index">
          <vs-card class="m-0 p-2">
            <div class="flex justify-between">
              <div>
                <h5 class="card-title tx-dark">{{ contactMechCard.contactMechTypeDescription }}</h5>
              </div>
              <div>
                <span @click="deleteBranchContactMech(contactMechCard.contactMechId)">
                  <i class="far fa-lg fa-trash-alt text-danger cursor-pointer"></i>
                </span>
              </div>
            </div>
            <p class="card-subtitle mb-4">{{ contactMechCard.contactMechPurposeDescription }}</p>
            <p class="card-text">{{ contactMechDetails(contactMechCard) | placeholder('-') }}</p>
          </vs-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import BranchService from '@/services/bank/BranchService'
import ContactMechService from '@/services/nucleus/ContactMechService'

export default {
  name: 'branch-contact',
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    branchId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      contactMechTableData: [],
      contactMechTableFilter: null,
      contactMechTableFilterTimer: null,
      contactMechTableOrderBy: null,
      contactMechTableContainer: 'contact-loading-container',
      contactMechTableBusy: false,
      contactMechTableTotalRows: 0,
      contactMechTableCurrentPage: 1,
      contactMechTablePerPage: 10,
      contactMechFormVisible: false,
      infoString: '',
      countryCode: '',
      areaCode: '',
      contactNumber: '',
      extension: '',
      contactMechPurposes: [],
      contactMechPurposeId: 'PhonePrimary',
      contactMechTypeEnumId: 'CmtTelecomNumber',
      contactMechEmail: false,
      contactMechCardLayout: false
    }
  },
  computed: {
    contactMechTableNoDataText: function () {
      return this.contactMechTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    contactMechTableTotalPages: function () {
      return Math.ceil(this.contactMechTableTotalRows / this.contactMechTablePerPage)
    },
    contactMechTablePagingInfo: function () {
      return this.pagingInfo(this.contactMechTableCurrentPage, this.contactMechTablePerPage, this.contactMechTableTotalRows)
    }
  },
  methods: {
    loadContactMechTableData (loadingContainerId) {
      let self = this
      this.contactMechTableBusy = true
      this.contactMechTableContainer = loadingContainerId || this.contactMechTableContainer
      this.startLoading(this.contactMechTableContainer)
      BranchService.contactMechs({
        pageIndex: this.contactMechTableCurrentPage - 1,
        pageSize: this.contactMechTablePerPage,
        filter: this.contactMechTableFilter,
        orderByField: this.contactMechTableOrderBy,
        branchId: this.branchId
      }).then(function (response) {
        self.stopLoading(self.contactMechTableContainer)
        self.contactMechTableBusy = false
        self.contactMechTableTotalRows = response.data.totalRows
        self.contactMechTableData = response.data.contactMechList || []
      })
    },
    refreshContactMechTable () {
      this.loadContactMechTableData()
    },
    filterContactMechTable (filter) {
      let self = this
      if (this.contactMechTableFilter !== filter) {
        clearTimeout(this.contactMechTableFilterTimer)
        this.contactMechTableFilterTimer = setTimeout(() => {
          self.contactMechTableFilter = filter
          self.loadContactMechTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.contactMechTableFilterTimer)
      }
    },
    orderContactMechTable (orderBy, active) {
      this.contactMechTableOrderBy = (active ? '' : '-') + orderBy
      this.loadContactMechTableData()
    },
    showContactMechForm () {
      this.contactMechFormVisible = true
    },
    hideContactMechForm () {
      this.contactMechFormVisible = false
    },
    contactMechDetails (item) {
      if (item.contactMechTypeEnumId === 'CmtTelecomNumber') {
        let phoneNumber = '+'
        phoneNumber += item.countryCode ? item.countryCode : ''
        phoneNumber += item.areaCode ? '-' + item.areaCode : ''
        phoneNumber += item.contactNumber ? '-' + item.contactNumber : ''
        phoneNumber += item.extension ? 'x' + item.extension : ''
        return phoneNumber
      } else {
        return item.infoString
      }
    },
    createBranchContactMech () {
      let self = this
      BranchService.createContactMech({
        branchId: this.$route.params.id,
        infoString: this.infoString,
        countryCode: this.countryCode,
        areaCode: this.areaCode,
        contactNumber: this.contactNumber,
        extension: this.extension,
        contactMechTypeEnumId: this.contactMechTypeEnumId,
        contactMechPurposeId: this.contactMechPurposeId
      }).then(function (response) {
        self.refreshContactMechTable()
        self.hideContactMechForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    deleteBranchContactMech (contactMechId) {
      let self = this
      BranchService.deleteContactMech({
        branchId: this.$route.params.id,
        contactMechId: contactMechId
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshContactMechTable()
        }
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    loadContactPurposes () {
      let self = this
      ContactMechService.contactMechPurposes({
        contactMechTypeEnumId: this.contactMechTypeEnumId,
        orderByField: 'description',
        pageNoLimit: true
      }).then(function (response) {
        self.contactMechPurposes = response.data || []
        self.contactMechPurposeId = self.contactMechTypeEnumId === 'CmtTelecomNumber' ? 'PhonePrimary' : 'EmailPrimary'
      })
    },
    toggleMechType (mechType) {
      if (mechType === 'PHONE') {
        this.contactMechTypeEnumId = 'CmtTelecomNumber'
      } else if (mechType === 'EMAIL') {
        this.contactMechTypeEnumId = 'CmtEmailAddress'
      }
    }
  },
  mounted () {
    this.loadContactMechTableData()
    this.loadContactPurposes()
  },
  watch: {
    contactMechTableCurrentPage: function () {
      this.refreshContactMechTable()
    },
    contactMechTablePerPage: function () {
      this.refreshContactMechTable()
    },
    contactMechEmail: function () {
      this.contactMechTypeEnumId = this.contactMechEmail ? 'CmtEmailAddress' : 'CmtTelecomNumber'
      this.loadContactPurposes()
    }
  }
}
</script>
