let TimePickerMixin = {
  methods: {
    timeFromString (time) {
      let elements = time.split(':')
      return {
        HH: elements[0],
        mm: elements[1],
        ss: elements[2]
      }
    },
    timeToString (obj) {
      return obj.HH + ':' + obj.mm + ':' + obj.ss
    }
  }
}

export default TimePickerMixin
