<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isBankAll')">{{ $t('MODIFY') }}</vs-button>
      </div>
      <div class="vs-row flex hover mb-base">
        <div class="nv-col sm:w-1/2 w-full p-2">
          <p class="info-row">
            <span>{{ $t('ID') }}:</span>
            <span>{{ branch.branchId }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('EXTERNAL_ID') }}:</span>
            <span>{{ branch.externalBranchId | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('BRANCH_NAME') }}:</span>
            <span>
            <span :class="disabledClass(branch)">{{ branch.branchName | placeholder('-') }}</span>
          </span>
          </p>
          <p class="info-row">
            <span>{{ $t('BANK') }}:</span>
            <span><router-link v-if="branch.bankId" :to="{ name: 'ViewBank', params: { id: branch.bankId }}">{{ branch.bankName | placeholder('-') }}</router-link></span>
          </p>
          <p class="info-row">
            <span>{{ $t('SWIFT_CODE') }}:</span>
            <span>{{ branch.swiftCode | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('LOCAL_CODE') }}:</span>
            <span>{{ branch.localCode | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('RECORD_KEY') }}:</span>
            <span>{{ branch.recordKey | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('ACCEPTS_PAYMENTS') }}:</span>
            <span>{{ branch.acceptsPayments | prettyBool }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('CREATION_DATE') }}:</span>
            <span>{{ branch.creationDate | formatUnixTimestamp }}</span>
          </p>
        </div>
        <div class="nv-col sm:w-1/2 w-full p-2">
          <p class="info-row">
            <span>{{ $t('ADDRESS_1') }}:</span>
            <span>{{ branch.address1 | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('ADDRESS_2') }}:</span>
            <span>{{ branch.address2 | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('DISTRICT') }}:</span>
            <span>{{ branch.district | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('CITY') }}:</span>
            <span>{{ branch.city | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('PROVINCE') }}:</span>
            <span>{{ branch.province | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('COUNTRY') }}:</span>
            <span>{{ branch.countryName | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('POSTAL_CODE') }}:</span>
            <span>{{ branch.postalCode | placeholder('-') }} {{ branch.postalCodeExt | placeholder('') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('PO_BOX') }}:</span>
            <span>{{ branch.poBox | placeholder('-') }}</span>
          </p>
        </div>
      </div>

      <vs-collapse type="border" class="mb-base" v-if="bankFunctionList.length > 0">
        <vs-collapse-item :open="true">
          <div slot="header">
            {{ $t('FUNCTIONS') }}
          </div>
          <div class="vs-row flex justify-start">
            <div v-for="(bankFunction, itemIndex) in bankFunctionList" class="nv-col sm:w-1/6 w-full p-2" :key="itemIndex">
              <i class="text-primary mr-1" :class="bankFunction.iconClass"></i> <span :class="disabledClass(bankFunction)">{{ bankFunction.functionName }}</span>
            </div>
          </div>
        </vs-collapse-item>
      </vs-collapse>

      <vs-tabs>
        <vs-tab :label="$t('CONTACTS')">
          <branch-contact @show-alert="showAlert" :branchId="branchId"/>
        </vs-tab>
        <vs-tab :label="$t('WORK_SHIFTS')">
          <branch-work-shift @show-alert="showAlert" :branchId="branchId"/>
        </vs-tab>
      </vs-tabs>
    </nv-card>
  </div>
</template>

<script>
import BranchService from '@/services/bank/BranchService'
import AlertMixin from '@/mixins/commons/AlertMixin'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import BranchContact from '@/views/bank/branch/branch-view/BranchContact'
import BranchWorkShift from '@/views/bank/branch/branch-view/BranchWorkShift'

export default {
  name: 'branch-view',
  mixins: [AlertMixin, DisabledClassMixin, ListLayoutMixin],
  components: {
    BranchContact,
    BranchWorkShift
  },
  data: function () {
    return {
      branchId: this.$route.params.id,
      branch: {},
      bankFunctionList: []
    }
  },
  computed: {
    workShiftTableTotalPages: function () {
      return Math.ceil(this.workShiftTableTotalRows / this.workShiftTablePerPage)
    },
    workShiftTablePagingInfo: function () {
      return this.pagingInfo(this.workShiftTableCurrentPage, this.workShiftTablePerPage, this.workShiftTableTotalRows)
    }
  },
  methods: {
    async load () {
      let self = this
      BranchService.branches({
        branchId: this.branchId
      }).then(function (response) {
        self.branch = response.data.branchList[0] || {}
      })

      BranchService.branchFunctions({
        branchId: this.$route.params.id
      }).then(function (response) {
        self.bankFunctionList = response.data.branchBankFunctionList || []
      })
    },
    createWorkShift () {
      let self = this
      BranchService.createWorkShift({
        branchId: this.$route.params.id,
        shiftSeqId: this.shiftSeqId,
        startTime: this.timeToString(this.startTime),
        endTime: this.timeToString(this.endTime),
        weekDayEnumId: this.weekDayEnumId
      }).then(function (response) {
        self.refreshWorkShiftTable()
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    deleteWorkShift (shiftSeqId) {
      let self = this
      BranchService.deleteWorkShift({
        branchId: this.$route.params.id,
        shiftSeqId: shiftSeqId
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshWorkShiftTable()
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyBranch', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  }
}
</script>
